import { useEffect, useState } from 'react'
import { Account } from '../state/account/actions'
import { useGetCall } from 'hooks';

export default function useSupportedCurrencies(account?: Account): {
  currency: string
  betAmount: number
}[] {
  const [result, setResult] = useState<{ currency: string, betAmount: number }[]>([]);
  const getCall = useGetCall(account)
  
  useEffect(
    () => {
      getCall('/lotto/supported-currencies').then(data => setResult(data))
    },
    [getCall]
  )

  return result
}