import { createAction } from '@reduxjs/toolkit';

export interface Account {
  id: string
  ouid: number
  loginId: string
  loginTime: number
  status: string
  currency: string
  balance: number
  profiles: any
  scopes: string[]
  accessToken: string
}

export const setAccount = createAction<{
  id: string
  ouid: number
  loginId: string
  loginTime: number
  status: string
  currency: string
  balance: number
  profiles: any
  scopes: string[]
  accessToken: string
}>('account/setAccount')
export const clearAccount = createAction('account/clearAccount')
export const setBalance = createAction<{ balance: number }>('account/setBalance')