import { useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useAccountState } from 'state/account/hooks'
import { AppDispatch, AppState } from '../index'
import { ApplicationModal, setOpenModal } from './actions'

export function useBetAmount(): number | undefined {
  const { account } = useAccountState()

  return useSelector((state: AppState) => {
    if (account) {
      if (account.profiles && account.profiles['betAmount']) {
        return account.profiles['betAmount']
      } 
    
      return state.application.supportedCurrencies[account.currency]
    }

    // Return an default bet amount
    return 1000
  })
}

export function useModalOpen(modal: ApplicationModal): boolean {
  const openModal = useSelector((state: AppState) => state.application.openModal)
  return openModal === modal
}

export function useToggleModal(modal: ApplicationModal): () => void {
  const open = useModalOpen(modal)
  const dispatch = useDispatch<AppDispatch>()
  return useCallback(() => dispatch(setOpenModal(open ? null : modal)), [dispatch, modal, open])
}

export function useOpenModal(modal: ApplicationModal): () => void {
  const dispatch = useDispatch<AppDispatch>()
  return useCallback(() => dispatch(setOpenModal(modal)), [dispatch, modal])
}

export function useCloseModals(): () => void {
  const dispatch = useDispatch<AppDispatch>()
  return useCallback(() => dispatch(setOpenModal(null)), [dispatch])
}

export function useNavToggle(): () => void {
  return useToggleModal(ApplicationModal.NAV)
}

export function useLoginModalToggle(): () => void {
  return useToggleModal(ApplicationModal.LOGIN)
}

export function useTutorialModalToggle(): () => void {
  return useToggleModal(ApplicationModal.TUTORIAL)
}

export function useToggleSettings(): () => void {
  return useToggleModal(ApplicationModal.SETTINGS)
}