import React from 'react'
import { useTranslation } from 'react-i18next'
import {
  styled,
  Drawer,
  Box,
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Button,
  IconButton,
  FormControl,
  Typography,
  Autocomplete,
  TextField,
} from '@mui/material'
import { XCircle } from 'react-feather'
import { useModalOpen, useToggleSettings } from 'state/application/hooks'
import { ApplicationModal } from 'state/application/actions'
import { Label, StyledOutlinedInput } from '../styled'

const ScrollbarWrapper = styled('div')(() => ({
  overflowY: 'auto',
  overflowAnchor: 'none',
  touchAction: 'auto',
  position: 'relative',
  borderRight: '0px !important',
  height: 'calc(100vh - 156px)',
}))

interface Env {
  name: string
  value: string
}

export default function Settings() {
  const { t } = useTranslation()
  const open = useModalOpen(ApplicationModal.SETTINGS)
  const toggleSettings = useToggleSettings()

  const envs: Env[] = [
    { name: '比特浏览器', value: 'bit' },
  ]

  const handleEnvChange = (event: React.SyntheticEvent<Element, Event>, value: any) => {
    console.log({ event, value})
  }

  const handleOK = () => {
    console.log("OK")
  }

  return (
    <Drawer
      anchor={'right'}
      sx={{ '.MuiDrawer-paper': { borderRadius: '20px 0 0 20px' }}}
      open={open}
      onClose={toggleSettings}
    >
      <Card elevation={0} sx={{ minWidth: 360 }}>
        <CardHeader title="设置" action={
          <IconButton onClick={toggleSettings} aria-label="settings-close">
            <XCircle />
          </IconButton>
        } />
        <CardContent>
          <ScrollbarWrapper>
            <Box sx={{ '& .MuiFormControl-root': { width: '100%' }}}>
              <Typography component={Label}>{t('环境')}</Typography>
                <Autocomplete
                    disablePortal
                    options={envs}
                    getOptionLabel={(option) => option.name}
                    defaultValue={envs[0]}
                    renderInput={(params) => <TextField {...params} />}
                    onChange={handleEnvChange}
                    sx={{
                        '.MuiOutlinedInput-root': {
                            padding: 0,
                            fontSize: '0.875rem',
                        },
                        '.MuiOutlinedInput-root .MuiAutocomplete-input': {
                            padding: '0.375rem 0.75rem',
                        },
                        '& .MuiOutlinedInput-notchedOutline': {
                            '& > legend': {
                                float: 'left !important',
                            }
                        },
                    }}
                />
                <Typography component={Label}>{t('环境API')}</Typography>
                <FormControl variant="outlined">
                  <StyledOutlinedInput
                    type="text"
                    placeholder={t('环境API')}
                  />
                </FormControl>
            </Box>
          </ScrollbarWrapper>
        </CardContent>
        <CardActions sx={{ p: 2, position: 'fixed', bottom: 0 }}>
          <Button disableElevation variant="contained" color="secondary" onClick={handleOK}>确定</Button>
        </CardActions>
      </Card>
    </Drawer>
  )
}