import React, { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import {
  DialogContent,
  DialogActions,
  Button,
  Typography,
  FormControl,
} from '@mui/material'
import { Label, Modal, ModalTitle, StyledOutlinedInput } from '../../styled'
import GroupSelect from './GroupSelect'
import CountrySelect from './CountrySelect'
import { CC, Country } from 'state/cc/actions'
import { Group } from 'state/group/actions'

interface State {
  name: string
  group: string
  country?: Country
  numbers: string[]
}

export default function GroupModal({ open, value, onChange, onDismiss }: {
  open: boolean
  value?: CC
  onChange?: (value: CC) => void
  onDismiss?: () => void
}) {
  const { t } = useTranslation()
  const [values, setValues] = useState<State>({
    name: '',
    group: '',
    country: undefined,
    numbers: [],
  })

  useEffect(() => {
    if (open && value) {
      setValues({ ...value })
    } else {
      setValues({ name: '', group: '', country: undefined, numbers: [], })
    }
  }, [value, open])

  const handleChange = (prop: keyof State) => (event: React.ChangeEvent<HTMLInputElement>) => {
    if (prop === 'numbers') {
      setValues({ ...values, [prop]: event.target.value.split("\n") })
    } else {
      setValues({ ...values, [prop]: event.target.value })
    }
  }

  const handleKeyUp = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event && event.key === 'Enter') {
      handleOK()
    }
  }

  const handleGroupChange = (value: Group | null) => {
    setValues({ ...values, group: value ? value.name : '' })
  }

  const handleCountryChange = (value: Country | null) => {
    setValues({ ...values, country: value ?? undefined })
  }

  const handleOK = () => {
    if (onChange) {
      if (value) {
        onChange({ ...value, ...values })
      } else {
        const time = Math.floor(new Date().getTime() / 1000)
        onChange({ ...values, id: `${time}`, createdTime: time })
      }
    }

    handleClose()
  }

  const handleClose = () => {
    onDismiss && onDismiss()
  }

  return (
      <Modal
        aria-labelledby="gruop-modal-title"
        maxWidth={'sm'}
        fullWidth={true}
        open={open}
        onClose={handleClose}
      >
        <ModalTitle id="gruop-modal-title" onClose={handleClose}>
          {value ? t('修改分组') : t('创建联系人')}
        </ModalTitle>
        <DialogContent dividers sx={{ '> .MuiFormControl-root': { width: '100%' }}}>
          <Typography component={Label}>{t('名称')}</Typography>
          <FormControl variant="outlined">
            <StyledOutlinedInput
              autoFocus
              type="text"
              value={values.name}
              placeholder={t('填写名称')}
              onChange={handleChange('name')}
              onKeyUp={handleKeyUp}
            />
          </FormControl>
          <Typography component={Label}>{t('分组')}</Typography>
          <FormControl variant="outlined">
            <GroupSelect value={{ id: '', name: values.group, createdTime: 0 }} onChange={handleGroupChange} />
          </FormControl>
          <Typography component={Label}>{t('国家')}</Typography>
          <FormControl variant="outlined">
            <CountrySelect value={values.country} onChange={handleCountryChange} />
          </FormControl>
          <Typography component={Label}>{t('号码')}</Typography>
          <FormControl variant="outlined">
            <StyledOutlinedInput
              multiline={true}
              minRows={5}
              maxRows={20}
              placeholder={t('号码|月份|年份|CVV')}
              value={values.numbers.join('\n')}
              onChange={handleChange('numbers')}
            />
          </FormControl>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleOK}>
            {t('确定')}
          </Button>
        </DialogActions>
      </Modal>
  )
}
