import React, { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import {
  styled,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  Typography,
  FormControl,
} from '@mui/material'
import { X } from 'react-feather'
import { Label, StyledOutlinedInput } from '../../styled'

import { Group } from 'state/group/actions'

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  
  '& .MuiDialogContent-root': {
    padding: theme.spacing(2),
  },
  '& .MuiDialogActions-root': {
    padding: theme.spacing(1),
  },
}));

export interface DialogTitleProps {
  id: string
  children?: React.ReactNode
  onClose: () => void
}

const BootstrapDialogTitle = (props: DialogTitleProps) => {
  const { children, onClose, ...other } = props;

  return (
    <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <X />
        </IconButton>
      ) : null}
    </DialogTitle>
  )
}

interface State {
  name: string
}

export default function GroupModal({ open, value, onChange, onDismiss }: {
  open: boolean
  value?: Group
  onChange?: (value: Group) => void
  onDismiss?: () => void
}) {
  const { t } = useTranslation()
  const [values, setValues] = useState<State>({ name: '' })

  useEffect(() => {
    if (open && value) {
      setValues({ name: value.name })
    } else {
      setValues({ name: '' })
    }
  }, [value, open])

  const handleChange = (prop: keyof State) => (event: React.ChangeEvent<HTMLInputElement>) => {
    setValues({ ...values, [prop]: event.target.value })
  }

  const handleKeyUp = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event && event.key === 'Enter') {
      handleOK()
    }
  }

  const handleOK = () => {
    if (onChange) {
      if (value) {
        onChange({ ...value, ...values })
      } else {
        const time = Math.floor(new Date().getTime() / 1000)
        onChange({ ...values, id: `${time}`, createdTime: time })
      }
    }

    handleClose()
  }

  const handleClose = () => {
    onDismiss && onDismiss()
  }

  return (
      <BootstrapDialog
        aria-labelledby="gruop-modal-title"
        maxWidth={'sm'}
        fullWidth={true}
        open={open}
        onClose={handleClose}
      >
        <BootstrapDialogTitle id="gruop-modal-title" onClose={handleClose}>
          {value ? t('修改分组') : t('创建分组')}
        </BootstrapDialogTitle>
        <DialogContent dividers>
          <Typography component={Label}>{t('名称')}</Typography>
          <FormControl variant="outlined" sx={{ width: '100%' }}>
            <StyledOutlinedInput
              autoFocus
              type="text"
              value={values.name}
              placeholder={t('填写名称')}
              onChange={handleChange('name')}
              onKeyUp={handleKeyUp}
            />
          </FormControl>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleOK}>
            {t('确定')}
          </Button>
        </DialogActions>
      </BootstrapDialog>
  )
}
