import React, { useCallback, useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import {
    styled, 
    Container, 
    Box, 
    Typography, 
    Avatar,
    FormControlLabel,
    Checkbox,
    Button,
    FormControl,
    OutlinedInput,
    InputAdornment,
    IconButton,
    CircularProgress,
 } from '@mui/material'
import { Eye, EyeOff } from 'react-feather'

import { useAccountState } from '../../state/account/hooks'
import { LoginState, useLoginCallback } from '../../hooks/useLoginCallback'

const BrandName = styled(Typography)(() => ({
  color: '#376fd0',
  marginBottom: '2rem',
  fontSize: '32px',
  fontWeight: '700',
}))

const Label = styled('label')(() => ({
  margin: '1rem 0px 0.5rem !important',
  fontWeight: '500',
  fontSize: '0.875rem',
  lineHeight: '1.5',
  fontFamily: '"DM Sans", sans-serif',
  display: 'block',
}))

const StyledOutlinedInput = styled(OutlinedInput)(() => ({
  '& .MuiOutlinedInput-notchedOutline': {
      '& > legend': {
          float: 'left !important',
      }
  },
}));

interface State {
  loginId: string
  password: string
  showPassword: boolean
  loginErrorMessage?: string
}

export default function Dashboard() {
  const { t } = useTranslation()
  const { account } = useAccountState()

  const [values, setValues] = useState<State>({
    loginId: account?.loginId ?? '',
    password: '',
    showPassword: false,
    loginErrorMessage: undefined
  })

  const isValid = values.loginId && values.loginId.length > 0 && values.password && values.password.length > 0

  const [login, loginCallback] = useLoginCallback(values.loginId, values.password)
  const [loginSubmitted, setLoginSubmitted] = useState<boolean>(false)

  useEffect(() => {
    if (login === LoginState.LOGGED) {
      setLoginSubmitted(false)
    }
  }, [login, loginSubmitted])

  const handleLogin = useCallback(
    () => {
      if (isValid) {
        setLoginSubmitted(true)

        loginCallback()
          .catch(({ response: { status, data } }) => {
            if (status === 403) {
              setValues({ ...values, loginErrorMessage: data.message })
            }
            setLoginSubmitted(false)
          })
      }
    },
    [values, isValid, loginCallback]
  )

  const handleChange = (prop: keyof State) => (event: React.ChangeEvent<HTMLInputElement>) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleFocus = (prop: keyof State) => () => {
    setValues({ ...values, loginErrorMessage: undefined, [prop]: '' })
  }

  const handleKeyUp = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (values.loginErrorMessage) {
      setValues({ ...values, loginErrorMessage: undefined })
    }

    if (event && event.key === 'Enter') {
      handleLogin()
    }
  }

  const handleClickShowPassword = () => {
    setValues({
      ...values,
      showPassword: !values.showPassword,
    });
  };

  const handleMouseDownPassword = (event: React.MouseEvent<HTMLButtonElement>) => {
    event.preventDefault();
  };

  return (
    <Container sx={{
      maxWidth: '520px !important',
      margin: '0 auto',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      minHeight: '100%',
      flexDirection: 'column',
    }}>
      <BrandName>WEA</BrandName>
      <Box sx={{
        backgroundColor: '#FFF',
        color: 'rgba(0, 0, 0, 0.87)',
        transition: 'box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
        borderRadius: '0.5rem',
        backgroundImage: 'none',
        padding: '2rem',
        width: '100%',
        boxShadow: 'rgb(90 114 123 / 11%) 0px 7px 30px 0px',
      }}>
        <Avatar alt="We Agency" sx={{ 
          width: 92, 
          height: 92,
          textAlign: 'center',
          margin: '0 auto 20px',
        }}>
          WA
        </Avatar>
        <Typography variant='h1' sx={{
          margin: '0',
          fontSize: '1.125rem',
          fontWeight: '500',
          lineHeight: '1.25',
          textAlign: 'center',
          marginBottom: '0.35em'
        }}>
          Welcome to WeAgency!
        </Typography>
        <Typography variant='h2' sx={{
          margin: '0',
          fontSize: '0.875rem',
          fontWeight: '400',
          lineHeight: '1.5',
          textAlign: 'center',
        }}>
          Sign in to your account to continue
        </Typography>
        <Box sx={{ mt: '2rem' }}>
          <Typography component={Label}>{t('Login Id')}</Typography>
          <FormControl sx={{ width: '100%' }} variant="outlined">
            <StyledOutlinedInput
              id="loginId-input"
              type="text"
              value={values.loginId}
              onChange={handleChange('loginId')}
              onFocus={handleFocus('loginId')}
              onKeyUp={handleKeyUp}
            />
          </FormControl>

          <Typography component={Label}>{t('Password')}</Typography>
          <FormControl sx={{ width: '100%' }} variant="outlined">
            <StyledOutlinedInput
              id="password-input"
              type={values.showPassword ? 'text' : 'password'}
              value={values.password}
              onChange={handleChange('password')}
              onFocus={handleFocus('password')}
              onKeyUp={handleKeyUp}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={handleClickShowPassword}
                    onMouseDown={handleMouseDownPassword}
                    edge="end"
                  >
                    {values.showPassword ? <EyeOff /> : <Eye />}
                  </IconButton>
                </InputAdornment>
              }
            />
          </FormControl>

          <Box sx={{
            margin: '1rem 0',
            display: 'flex',
            justifyContent: 'space-between'
          }}>
            <FormControlLabel control={<Checkbox defaultChecked />} label="Remeber me" />
            <Typography component='a' href="#" sx={{ lineHeight: '2.5' }}>Forgot Password ?</Typography>
          </Box>

          <Button 
            variant="contained" 
            fullWidth={true}
            color={values.loginErrorMessage ? 'error' : 'primary'}
            disabled={!isValid || loginSubmitted || login === LoginState.LOGGED}
            sx={{
              padding: '0.375rem 0.75rem',
              fontSize: '1rem',
              height: '40px'
            }}
            onClick={handleLogin}
          >
            {loginSubmitted 
              ? <CircularProgress color='inherit' size="2rem" />
              : t(values.loginErrorMessage ? values.loginErrorMessage : 'Sign In')}
          </Button>
        </Box>
      </Box>
    </Container>
  )
}