import { useEffect } from 'react'
import { useDispatch } from 'react-redux'
import useSupportedCurrencies from 'hooks/useSupportedCurrencies'
import { updateSupportedCurrency } from './actions'
import { useAccountState } from 'state/account/hooks'

export default function Updater(): null {
  const { account } = useAccountState()
  const supportedCurrencies = useSupportedCurrencies(account)
  const dispatch = useDispatch()

  useEffect(
    () => {
      supportedCurrencies && supportedCurrencies.forEach(e => dispatch(updateSupportedCurrency(e)))
    }, 
    [dispatch, supportedCurrencies]
  )

  return null
}
