import { useCallback } from 'react'
import { Account } from 'state/account/actions'
import { Task } from 'state/task/actions'

export function useUserTasks(account?: Account)
  : (page: number, pageSize: number) => Promise<{ total: number; result: Task[] }> {

  return useCallback(
    (page: number, pageSize: number) => {
      if (account) {
        return new Promise((resolve, reject) => resolve({ 
            total: 0,
            result: [] 
        }))
      } else {
        return new Promise((resolve, reject) => resolve({ total: 0, result: [] }))
      }
    },
    [account]
  )
}