import { useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Account } from 'state/account/actions'
import { useAccountState } from 'state/account/hooks'
import { AppDispatch, AppState } from '../index'
import { Group, addGroup, removeGroup } from "./actions"

export function useActionHandlers(account?: Account): {
  onAdd: (group: Group) => void
  onRemove: (group: Group) => void
} {
  const dispatch = useDispatch<AppDispatch>();

  const onAdd = useCallback(
    (group: Group) => {
      account && dispatch(addGroup({ accountId: account.id, group }))
    },
    [dispatch, account]
  )

  const onRemove = useCallback(
    (group: Group) => {
      account && dispatch(removeGroup({ accountId: account.id, group }))
    },
    [dispatch, account]
  )

  return {
    onAdd,
    onRemove,
  };
}

export function useAllGroups(): { [id: string]: Group } {
  const { account } = useAccountState()
  const state =  useSelector<AppState, AppState['group']>(state => state.group)

  return account ? state[account.id] ?? {} : {}
}