import { useCallback } from 'react'
import { Account } from 'state/account/actions'
import { Group } from 'state/group/actions'

export function useUserGroups(account?: Account)
  : (page: number, pageSize: number) => Promise<{ total: number; result: Group[] }> {

  return useCallback(
    (page: number, pageSize: number) => {
      if (account) {
        return new Promise((resolve, reject) => resolve({ 
            total: 0,
            result: [] 
        }))
      } else {
        return new Promise((resolve, reject) => resolve({ total: 0, result: [] }))
      }
    },
    [account]
  )
}