import { createReducer } from '@reduxjs/toolkit';
import { setAccount, clearAccount, setBalance, Account } from './actions';

export interface AccountState {
   account: Account | undefined
}

const initialState: AccountState = {
  account: undefined,
}

export default createReducer<AccountState>(initialState, (builder) =>
  builder
    .addCase(
      setAccount, (state, { 
        payload: { id, ouid, loginId, loginTime, status, currency, balance, profiles, scopes, accessToken } 
      }) => {
        state.account = { 
          id, 
          ouid, 
          loginId, 
          loginTime, 
          status, 
          currency, 
          balance, 
          profiles, 
          scopes, 
          accessToken 
        }
      }
    )
    .addCase(clearAccount, (state) => {
      state.account = undefined
    })
    .addCase(setBalance, (state, { payload: { balance } }) => {
      if (state.account) {
        state.account.balance = balance;
      }
    })
);